@import 'mixins';

header
{
  display: flex;
  justify-content: space-between;
  button  
  {
    @include home-btn();
  }
  #turn
  {
    background-color: var(--dark-green);
    color: var(--gray);
    display: flex;
    align-items: center;
    gap: 10px;
    font-size: 1rem;
    font-weight: bolder;
    padding: 10px 30px;
    border-radius: 10%;
    @include box-shadow(0px, 7px, 0px, #0F2027);
    i
    {
      font-size: 1.5rem;
    }
  }
}