.score
{
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: center;
  align-items: center;
  background-color: var(--gray);
  border-radius: 10px;
  padding: 10px 0;
  .title
  {
    font-size: 1rem;
    i
    {
      margin-right: 10px;
    }
  }
  .value
  {
    font-size: 2rem;
    font-weight: bolder;
  }
}
