@import 'mixins';

#end-game
{
  width: 100vw;
  height: var(--end-game-height);
  position: absolute;
  top: calc(50% - var(--end-game-height) / 2);
  background-color: var(--dark-green);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: var(--white);
  z-index: 2;
  gap: 20px;
  #winner
  {
    font-size: 1rem;
  }
  #takes
  {
    font-size: 2rem;
    font-weight: bolder;
    i
    {
      margin-right: 10px;
    }
  }
  .btn-container
  {
    display: flex;
    gap: 20px;
    .home-btn
    {
      @include home-btn();
    }
    #btn-next-round
    {
      font-size: 1rem;
      font-weight: bolder;
      padding: calc(var(--end-game-height) * 0.1);
      border-radius: 10px;
      color: var(--darker-green);
      background-color: var(--gray);
      @include box-shadow(0, 7px, 0, #718794);
    }
  }
}